.our_team {
    display: flex;
    flex-direction: column;
    text-align: center;
    background: #f2f2f2;
    padding: 30px;
    .wrap {
        display: flex;
        justify-content: space-around;
        .people {
            text-align: center;
            margin-bottom: 15px;
            h4 {
                color: $gray-dark;
                padding-bottom: 5px;
                span {
                    color: $primary-color;
                    border: none;
                }
            }
            p {
                color: $gray-text;
            }
            a {
                color: $gray-dark;
                font-size: 10px;
                text-decoration: none;
                &:hover {
                    color: $gray-light;
                }
            }
        }
    }
}