.recive {
    margin-top: 0;
    text-align: center;
    background: $white-text;
    padding: 70px 50px 10px 50px;
    color: $gray-light;
    p {
        font-size: 22px;
        margin-bottom: 50px;
    }
    .icons {
        display: flex;
        h4 {
            text-decoration: underline;
            text-transform: uppercase;
        }
        p {
            font-size: 18;
        }
        div {
            padding: 15px;
        }
    }
}