@media screen and (max-width: 768px) {
    .wedo1 {
        padding: 5px;
        text-align: center;
        .container {
            display: flex;
            flex-direction: column;
            align-items: center;
            .left {
                order: 2;
                max-width: 100%;
                margin-bottom: 30px;
                .btn {
                    margin-bottom: 20px;
                }
            }
            .right {
                order: 1;
                max-width: 100%;
                img {
                    width: 100%;
                }
            }
        }
    }
}