a.arrowDown {
    font-size: 30px;
    text-decoration: none;
    color: $primary-color;
    border: 1px solid $primary-color;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    &:hover {
        background: $primary-color;
        color: $white-text;
    }
}