@media screen and (max-width: 1260px) {
    .recive {
        padding: 30px 20px;
        h2 {
            font-size: 26px;
        }
        p {
            font-size: 18px;
            margin-bottom: 20px;
        }
        .icons {
            flex-wrap: wrap;
            justify-content: center;
            div {
                max-width: 350px;
                p {
                    margin-bottom: 20px;
                }
            }
        }
    }
}