.wedo2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px;
    .container {
        display: flex;
        justify-content: space-around;
        .left {
            order: 2;
            max-width: 50%;
            text-align: right;
            h3 {
                display: inline-block;
                color: $gray-text;
                text-transform: uppercase;
                border-right: 10px solid $primary-color;
                padding-right: 5px;
            }
            p {
                padding-bottom: 15px;
                font-size: 18px;
                color: $gray-text;
            }
            a {
                text-align: right;
            }
        }
        .right {
            order: 1;
            max-width: 50%;
        }
    }
}