header {
    nav {
        display: flex;
        justify-content: space-around;
        align-items: center;
        >a {
            color: $white-text;
            font-size: 25px;
            font-weight: bold;
            text-decoration: none;
            img {
                padding-right: 15px;
                vertical-align: middle;
            }
        }
        .logo:hover img {
            opacity: 0.7;
        }
        menu {
            list-style: none;
            text-transform: uppercase;
            display: flex;
            li {
                padding-left: 15px;
                a {
                    color: white;
                    font-size: 20px;
                    text-decoration: none;
                    &:hover {
                        color: $primary-color;
                        border-bottom: 3px solid $primary-color;
                        padding-bottom: 5px;
                    }   
                }
            }
        }
    }
}