.portfolio {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-bottom: 30px;
    .controls {
        text-align: center;
        letter-spacing: 2px;
        font-size: 25px;
        border-top: 1px solid gray;
        border-bottom: 1px solid gray;
        padding: 15px;
        margin-bottom: 30px;
        button {
            color: $primary-color;
            background: transparent;
            border: none;
            text-transform: uppercase;
            font-size: 22px;
            &:hover {
                color: $primary-color-dark;
                cursor: pointer;
            }
        }
    }
    .container-filter {
        width: 100%;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        div {
            background-size: cover;
            background-position: center center;
            margin: 10px;
            width: 200px;
            height: 180px;
            .fil {
                font-size: 20px;
                text-transform: uppercase;
                color: $white-text;
                font-weight: bold;
                display: none;
                position: relative;
                margin: 0;
                background: url('../img/portfolio/plus.png') $primary-color-rgba;
                background-position: center;
                background-repeat: no-repeat;
                p {
                    margin: 0;
                    padding: 10px;
                }
            }
            &:hover .fil {
                cursor: pointer;
                display: block;
            }
        }
        #ex1 {
            background-image: url('../img/portfolio/1.png');
        }
        #ex2 {
            background-image: url('../img/portfolio/2.png');
        }
        #ex3 {
            background-image: url('../img/portfolio/3.png');
        }
        #ex4 {
            background-image: url('../img/portfolio/4.png');
        }
        #ex5 {
            background-image: url('../img/portfolio/5.png');
        }
        #ex6 {
            background-image: url('../img/portfolio/6.png');
        }
    }
    .btn {
        margin: 40px 0px;
    }
}