.mainText {
    text-align: center;
    p {
        color: $white-text;
        font-size: 22px;
    }
}

h2 {
    text-transform: uppercase;
    font-size: 45px;
    color: $gray-text;
    margin: 20px 0px;
    padding-bottom: 5px;
    display: inline-block;
    position: relative;
    &::before {
        position: absolute;
        display: block;
        content: '';
        height: 3px;
        width: 100%;
        left: 0;
        bottom: 0;
        background: $gray-light;
    }
    span {
        color: $primary-color;
    }
}