.wedo1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px;
    background: #f2f2f2;
    .container {
        display: flex;
        justify-content: space-around;
        .left {
            max-width: 50%;
            h3 {
                display: inline-block;
                color: $gray-text;
                text-transform: uppercase;
                border-left: 10px solid $primary-color;
                padding-left: 5px;
            }
            p {
                padding-bottom: 15px;
                font-size: 18px;
                color: $gray-text;
            }
        }
        .right {
            max-width: 50%;
        }
    }
}